    monospace;
} 

.punchItAnimation-gbl {
  animation: punchIt 0.5s ;
  visibility: hidden;
  animation-fill-mode: forwards;
  }

@keyframes punchIt{
  0% {
      scale: 4;
      opacity: 0;
      
  }
      50% {
      opacity: 0.5;

      }
      100% {
      scale: 1;
      opacity: 1;
      visibility: visible;
      }
  }

 .bounceItAnimation-gbl { 
  background-color: #EE6C1B;
  display: inline-block;
  animation: bounceIt 3s;
  animation-iteration-count: infinite;
 }
  @keyframes bounceIt{
    0% {
    transform: scale(1);
    }
    50%{
    transform: scale(1.05);
    }
    100% {
    transform: scale(1);
    }
    }
    .punchItRandomAnimation-gbl {
      animation: punchItRandom 2s ;
      visibility: hidden;
      animation-fill-mode: forwards;
      }
    @keyframes punchItRandom{
      0% {
          scale: 4;
          opacity: 0;
          transform-origin: right
          
      }
      25%{
        transform-origin: left;;
      }
          50% {
          opacity: 0.5;
          transform-origin: right;
    
          }
          100% {
          scale: 1;
          opacity: 1;
          visibility: visible;
          }
      }
 
      @media  (max-width:960px) {
        html {
          font-size: 12px;
        }
      }

